<template lang="pug">
  .line-chart__content
    line-chart(
       :data='chartData'
       :labels='labels'
       :options='options'
    )
</template>

<script>
  import LineChart from './chartDepartmentsScholars.js'
  export default {
    components: {
      LineChart
    },
    props: {
      chartData: Array
    },
    data () {
      return {
        backgroundColor: [
          '#346f79',
          '#fead00',
          '#80000d',
          '#555555',
          '#bf3300',
          '#b97b2a',
          '#6e3b00',
          '#171442', 
          '#e6000a',
          '#a43cbe',
          '#226ab7',
          '#ff7600',
          '#00b229',
          '#925148',
          '#7f7f7f'
        ],
        datacollection: {},
        options : {
            scales: {
                yAxes: [{
                    ticks: {
                        min: 0,
                        stepSize: 1
                    }
                }]
            }    
        }   
      }
    },
    async mounted(){
        await this.fillData()
    },
    computed: {
      labelArray: function(){ 
        return [...new Set(this.chartData.map(ele=> ele.department.name))]
      },
      labels: function(){ 
        return this.chartData.filter(ele=> ele.department.name==this.labelArray[0]).map(ele => ele.date)
      }
    },
    methods: {
      makeData(departmentName) {
        return this.chartData.filter(ele=> ele.department.name==departmentName).map(ele => ele.count)
      },
      fillData(){
        var chartArray = []
        this.labelArray.forEach(element => {
          chartArray.push({
              label: element,
              data:  this.makeData(element),
              borderColor: '#000',
              borderWidth: 2,
              pointRadius: 5,
              backgroundColor: this.backgroundColor
            }) 
        });
        this.datacollection = {
          labels: this.labels,
          datasets: chartArray
        }
      }
    },

  }
</script>

<style lang="scss">
  .line-chart__content {
    width: 100%;
    background: #ffe9ce;
    border-radius: 6px;
    padding: 3%;
  }
</style>