import { Line, mixins } from 'vue-chartjs'
const { reactiveProp } = mixins

export default {
  extends: Line,
  mixins: [reactiveProp],
  props: ['data','labels','options'],
  mounted () {
    // this.chartData is created in the mixin.
    // If you want to pass options please create a local options object
    this.renderLineChart();
  },
  data () {
    return {
      fillcolors: [
        '#d86d93',
        '#9379f8',
        '#ff8800',
        '#fde435',
        '#ec113b',
        '#5352b0',
        '#3c9163',
        '#afdc47',
        '#ffc353'
    ],
      backgroundColor: [
        '#346f79',
        '#fead00',
        '#80000d',
        '#555555',
        '#bf3300',
        '#b97b2a',
        '#6e3b00',
        '#171442', 
        '#e6000a',
        '#a43cbe',
        '#226ab7',
        '#ff7600',
        '#00b229',
        '#925148',
        '#7f7f7f',
        '#346f79',
        '#fead00',
        '#80000d',
        '#555555',
        '#bf3300',
        '#b97b2a',
        '#6e3b00',
        '#171442', 
        '#e6000a',
        '#a43cbe',
        '#226ab7',
        '#ff7600',
        '#00b229',
        '#925148',
        '#7f7f7f' 
      ]
    }
  },
  computed: {
    labelArray: function(){ 
      return [...new Set(this.data.map(ele=> ele.department.name))]
    }
  },
  methods: {
    makeData(departmentName) {
      return this.data.filter(ele=> ele.department.name==departmentName).map(ele => ele.count)
    },
    renderLineChart: function() {
      var chartArray = []
        this.labelArray.forEach((element, idx) => {
          chartArray.push({
              label: element,
              data:  this.makeData(element),
              borderColor: this.fillcolors[idx],
              pointBackgroundColor: this.backgroundColor,
              fill: false,
              borderWidth: 2,
              pointRadius: 5,
              backgroundColor: this.backgroundColor
            }) 
        });
    this.renderChart({
      labels: this.labels,
      datasets: chartArray
    }, this.options)
  }
  },  
  watch: {
    data: function() {
      this.renderLineChart();
    }
  }
}